import { Formik, Form, Field, ErrorMessage } from "formik";
import logo from "../../assets/images/safa-logo.svg";
import google from "../../assets/images/google.png";
import group7 from "../../assets/images/Group7.png";
import group342 from "../../assets/images/Group342.png";

import styles from "./styles.module.scss";
import Validation from "../../core/Helpers/validation";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFacebookSquare,
  faTwitter,
} from "@fortawesome/free-brands-svg-icons";
import { Container, Row, Col } from "react-bootstrap";
import { useTranslation } from "react-i18next";

export default function Signup() {
const { t } = useTranslation();
  return (
    <div
      className={`${styles.main} d-flex align-items-center justify-content-center flex-column py-5`}
    >
      <div className={styles.top}>
        <img src={group7} alt="circles" className="w-100" />
      </div>
      <div className="mb-5">
        <img src={logo} alt="logo" />
      </div>
      <div
        className={`${styles.bgLight} d-flex align-item-center flex-column  p-5 mx-3 `}
        style={{ zIndex: 1 }}
      >
        <h1 className="text-center mb-5" style={{ color: "#5260AB" }}>
              {t('signup.title')}
        </h1>
        <Formik
          initialValues={{
            username: "",
            usernameEn: "",
            nationality: "",
            id: "",
            phone: "",
            email: "",
            job: "",
            qualification: "",
            sector: "",
            addmission: false,
          }}
          validationSchema={Validation().RegisterSchema}
          onSubmit={(values, { setSubmitting }) => {
            setTimeout(() => {
              alert(JSON.stringify(values, null, 2));
              setSubmitting(false);
            }, 400);
          }}
        >
          {({ isSubmitting, errors }) => (
            <Form>
              <Container fluid>
                <Row className=" mb-3  ">
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="username"
                        className={`${styles.bgLight}  py-1 px-2 `}
                      >
                       {t('signup.nameLabal')}
                      </label>
                      <Field
                        type="text"
                        name="username"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.username ? `${styles.error}` : ""
                        }`}
                      />
                      <ErrorMessage
                        name="username"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <label
                        htmlFor="usernameEn"
                        className={`${styles.bgLight} py-1 px-2 `}
                      >
                         {t('signup.nameLabelEn')}
                      </label>
                      <Field
                        type="text"
                        name="usernameEn"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.usernameEn ? `${styles.error}` : ""
                        }`}
                      />
                      <ErrorMessage
                        name="usernameEn"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                </Row>

                <Row className="  mb-3  ">
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="nationality"
                        className={`${styles.bgLight} py-1 px-2 `}
                      >
                        {t('signup.nationalityLabel')}
                      </label>
                      <Field
                        type="text"
                        name="nationality"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.nationality ? `${styles.error}` : ""
                        }`}
                      />
                      <ErrorMessage
                        name="nationality"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="id"
                        className={`${styles.bgLight} t py-1 px-2`}
                      >
                           {t('signup.idLabel')}
                      </label>
                      <Field
                        type="text"
                        name="id"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.id ? `${styles.error}` : ""
                        }`}
                      />
                      <ErrorMessage
                        name="id"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                </Row>
                <Row className=" mb-3  ">
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="phone"
                        className={`${styles.bgLight}  py-1 px-2`}
                      >
                         {t('signup.phoneLabel')}
                      </label>
                      <Field
                        type="text"
                        name="phone"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.phone ? `${styles.error}` : ""
                        }`}
                      />
                      <ErrorMessage
                        name="phone"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="email"
                        className={`${styles.bgLight}  py-1 px-2`}
                      >
                        {t('signup.emailLabel')}
                      </label>
                      <Field
                        type="email"
                        name="email"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.email ? `${styles.error}` : ""
                        }`}
                      />
                      <ErrorMessage
                        name="email"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                </Row>
                <Row className="  mb-3  ">
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="job"
                        className={`${styles.bgLight}  py-1 px-2`}
                      >
                      {t('signup.jobLabel')}
                      </label>
                      <Field
                        type="text"
                        name="job"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.job ? `${styles.error}` : ""
                        }`}
                      />
                      <ErrorMessage
                        name="job"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="qualification"
                        className={`${styles.bgLight} py-1 px-2`}
                      >
                         {t('signup.qualificationLabel')}
                      </label>
                      <select
                        name="qualification"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.qualification ? `${styles.error}` : ""
                        }`}
                      >
                        <option value="" label=""></option>
                        <option value="1المؤهل" label="1المؤهل">
                          1المؤهل
                        </option>
                        <option value="1المؤهل" label="  2المؤهل">
                          2المؤهل
                        </option>
                      </select>
                      <ErrorMessage
                        name="qualification"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                </Row>

                <Row className="  mb-3  ">
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="membership"
                        className={`${styles.bgLight}  py-1 px-2`}
                      >
                        العضوية
                      </label>
                      <select
                        name="membership"
                        className=" d-block mb-3 w-100 p-3"
                      >
                        <option value="" label=""></option>
                        <option value="membership1" label="membership2">
                          membership1
                        </option>
                        <option value="membership2" label="membership2">
                          membership2
                        </option>
                      </select>
                      <ErrorMessage
                        name="membership"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                  <Col xs={12} md={6}>
                    <div>
                      <label
                        htmlFor="sector"
                        className={`${styles.bgLight}  py-1 px-2`}
                      >
                        {t('signup.sectorLabel')}
                      </label>
                      <select
                        name="sector"
                        className={`d-block mb-3 w-100 p-3 ${
                          errors.sector ? `${styles.error}` : ""
                        }`}
                      >
                        <option value="" label=""></option>
                        <option value=" 1القطاع" label=" القطاع1">
                          1القطاع
                        </option>
                        <option value=" 2القطاع" label=" القطاع2">
                          2القطاع
                        </option>
                      </select>
                      <ErrorMessage
                        name="sector"
                        component="div"
                        className={styles.invalid}
                      />
                    </div>
                  </Col>
                </Row>
                <div className="d-block mb-3 ">
                  <Field type="checkbox" className="ms-2" name="admission" />
                  <label htmlFor="admission">
                     {t('signup.checkBox')}
                  </label>
                  <ErrorMessage
                    name="admission"
                    component="div"
                    className={styles.invalid}
                  />
                </div>
              </Container>

              <button
                type="submit"
                disabled={isSubmitting}
                className={` btn ${styles.btnPrimary}    py-3 px-5 mx-auto my-4 d-block `}
              >
                 {t('signup.login')}
              </button>
            </Form>
          )}
        </Formik>
        <h3 className="text-center py-4" style={{ color: "rgb(175 183 223)" }}>
          {t('signup.or')}
        </h3>
        <div
          className={`${styles.social}  d-flex align-items-center justify-content-center text-center flex-wrap `}
        >
          <a
            href="#"
            className="d-flex align-items-center justify-content-center px-5 py-3 fs-5 mb-3"
          >
            <FontAwesomeIcon icon={faTwitter} />
          </a>

          <a
            href="#"
            className="d-flex align-items-center justify-content-center py-3 px-5 fs-5 mx-3 mb-3"
          >
            <FontAwesomeIcon icon={faFacebookSquare} />
          </a>
          <a
            href="#"
            className="d-flex align-items-center justify-content-center py-3 px-5 fs-5 mb-3"
          >
            <img src={google} alt="googelImage" />
          </a>
        </div>
        <div className={styles.signup}>
          <p className="text-center p-3 ">
            <a href="#"> {t('signup.haveAccount')}</a>
            <a href="#"> {t('signup.signin')}</a>
          </p>
        </div>
      </div>
      <div className={styles.down}>
        <img src={group342} alt="circles" className="w-100" />
      </div>
    </div>
  );
}
